@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap');

body {
    font-family: 'Montserrat', sans-serif;
    margin: 0;
    padding: 0;
    background: linear-gradient(135deg, #1e1e2e, #2d2d42);
    min-height: 100vh;
    color: #ffffff;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
    background-color: #555555;
    padding: 0.125rem;
    border-radius: 0.25rem;
}

.app-container {
    min-height: 100vh;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.app-header {
    text-align: center;
    margin-bottom: 3rem;
    max-width: 600px;
}

.app-header h1 {
    font-size: 3rem;
    font-weight: 700;
    margin: 0 0 1rem 0;
    background: linear-gradient(45deg, #8b5cf6, #06b6d4);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.app-header p {
    font-size: 1.2rem;
    opacity: 0.8;
    line-height: 1.6;
    margin: 0;
}

.editor-container {
    width: 100%;
    max-width: 800px;
    flex: 1;
    display: flex;
    justify-content: center;
}

.editor-wrapper {
    background: #ffffff;
    border-radius: 12px;
    padding: 0;
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
    width: 100%;
    min-height: 400px;
    overflow: hidden;
}

.editor-header {
    display: flex;
    justify-content: flex-end;
    padding: 1rem 1.5rem;
    background: rgba(139, 92, 246, 0.05);
    border-bottom: 1px solid rgba(139, 92, 246, 0.1);
}

.copy-button {
    background: linear-gradient(45deg, #8b5cf6, #06b6d4);
    color: white;
    border: none;
    border-radius: 8px;
    padding: 0.5rem 1rem;
    font-size: 0.9rem;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 2px 4px rgba(139, 92, 246, 0.2);
    position: relative;
    overflow: hidden;
}

.copy-button::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
    transition: left 0.5s;
}

.copy-button:hover::before {
    left: 100%;
}

.copy-button:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(139, 92, 246, 0.4);
}

.copy-button.success {
    background: linear-gradient(45deg, #22c55e, #16a34a);
    animation: pulse 0.3s ease-in-out;
}

@keyframes pulse {
    0% { transform: scale(1); }
    50% { transform: scale(1.05); }
    100% { transform: scale(1); }
}

.tiptap {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    color: #333;
    font-size: 1.1rem;
    line-height: 1.8;
    outline: none;
    min-height: 300px;
    padding: 1.5rem;
}

button {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

/* Basic editor styles */
.tiptap :first-child {
    margin-top: 0;
}

.tiptap .mention {
    background-color: #f3e8ff;
    border-radius: 0.4rem;
    box-decoration-break: clone;
    color: #7e22ce;
    padding: 0.1rem 0.3rem;
}

.tiptap .mention::after {
    content: '\200B';
}

.main-content {
    display: flex;
    width: 100%;
    max-width: 1200px;
    gap: 2rem;
    flex: 1;
}

.sidebar {
    width: 300px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 12px;
    padding: 1.5rem;
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.1);
    height: fit-content;
}

.variable-manager h3 {
    margin: 0 0 1rem 0;
    font-size: 1.25rem;
    font-weight: 600;
}

.add-variable {
    display: flex;
    gap: 0.5rem;
    margin-bottom: 1.5rem;
    width: 100%;
}

.variable-input {
    flex: 1;
    padding: 0.75rem;
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 8px;
    background: rgba(255, 255, 255, 0.1);
    color: #ffffff;
    font-family: inherit;
    font-size: 0.9rem;
}

.variable-input::placeholder {
    color: rgba(255, 255, 255, 0.5);
}

.variable-input:focus {
    outline: none;
    border-color: #8b5cf6;
    box-shadow: 0 0 0 2px rgba(139, 92, 246, 0.2);
}

.add-button {
    padding: 0.75rem 1rem;
    background: linear-gradient(45deg, #8b5cf6, #06b6d4);
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    font-weight: 500;
    transition: all 0.2s;
    width: 100%;
}

.add-button:hover {
    transform: translateY(-1px);
    box-shadow: 0 4px 12px rgba(139, 92, 246, 0.3);
}

.variables-list {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}

.variable-item {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 0.75rem;
    background: rgba(255, 255, 255, 0.05);
    border-radius: 8px;
    border: 1px solid rgba(255, 255, 255, 0.1);
    transition: all 0.2s;
}

.variable-item:hover {
    background: rgba(255, 255, 255, 0.1);
}

.variable-item.editing {
    padding: 1rem;
    background: rgba(255, 255, 255, 0.1);
}

.variable-info {
    display: flex;
    flex-direction: column;
    gap: 0.25rem;
    flex: 1;
}

.variable-label {
    font-size: 0.9rem;
    color: #ffffff;
    font-weight: 500;
}

.variable-id {
    font-size: 0.75rem;
    color: rgba(255, 255, 255, 0.6);
    font-family: 'Courier New', monospace;
}

.variable-actions {
    display: flex;
    gap: 0.25rem;
    align-items: center;
}

.edit-button {
    background: rgba(139, 92, 246, 0.2);
    color: #8b5cf6;
    border: none;
    border-radius: 4px;
    width: 24px;
    height: 24px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    transition: all 0.2s;
}

.edit-button:hover {
    background: rgba(139, 92, 246, 0.3);
}

.remove-button {
    background: rgba(239, 68, 68, 0.2);
    color: #ef4444;
    border: none;
    border-radius: 4px;
    width: 24px;
    height: 24px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    line-height: 1;
    transition: all 0.2s;
}

.remove-button:hover {
    background: rgba(239, 68, 68, 0.3);
}

.save-button {
    background: rgba(34, 197, 94, 0.2);
    color: #22c55e;
    border: none;
    border-radius: 4px;
    width: 24px;
    height: 24px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    transition: all 0.2s;
}

.save-button:hover {
    background: rgba(34, 197, 94, 0.3);
}

.cancel-button {
    background: rgba(156, 163, 175, 0.2);
    color: #9ca3af;
    border: none;
    border-radius: 4px;
    width: 24px;
    height: 24px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    transition: all 0.2s;
}

.cancel-button:hover {
    background: rgba(156, 163, 175, 0.3);
}

.variable-edit-form {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    flex: 1;
    margin-right: 0.5rem;
}

.variable-edit-input {
    padding: 0.5rem;
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 6px;
    background: rgba(255, 255, 255, 0.1);
    color: #ffffff;
    font-family: inherit;
    font-size: 0.8rem;
}

.variable-edit-input:focus {
    outline: none;
    border-color: #8b5cf6;
    box-shadow: 0 0 0 2px rgba(139, 92, 246, 0.2);
}

.variable-id-edit {
    font-family: 'Courier New', monospace;
}

@media (max-width: 768px) {
    .app-container {
        padding: 1rem;
    }

    .app-header h1 {
        font-size: 2rem;
    }

    .app-header p {
        font-size: 1rem;
    }

    .editor-wrapper {
        padding: 0;
    }

    .editor-header {
        padding: 1rem;
    }

    .copy-button {
        font-size: 0.8rem;
        padding: 0.4rem 0.8rem;
    }

    .main-content {
        flex-direction: column;
        gap: 1rem;
    }

    .sidebar {
        width: 100%;
    }
}
