/* Dropdown menu */
.dropdown-menu {
    background: #ffffff; /* var(--white) */
    border: 1px solid #e0e0e0; /* var(--gray-1) */
    border-radius: 0.7rem;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.15); /* var(--shadow) */
    display: flex;
    flex-direction: column;
    gap: 0.1rem;
    overflow: auto;
    padding: 0.4rem;
    position: relative;
}

.dropdown-menu button {
    align-items: center;
    background-color: transparent;
    display: flex;
    gap: 0.25rem;
    text-align: left;
    width: 100%;
    border: none;
    padding: 0.5rem;
    border-radius: 0.25rem;
    font-size: 1rem;
}

.dropdown-menu button:hover,
.dropdown-menu button:hover.is-selected {
    background-color: #f0f0f0; /* var(--gray-3) */
}

.dropdown-menu button.is-selected {
    background-color: #f0f0f0; /* var(--gray-2) */
}
